


/*  */
.btn-wrapper button {
  background-color: red;
  border: 0px;
  padding: 5px;
  margin-left: 5px;
  border-radius: 4px;
  color: #fff;
  box-shadow: #333 0px 1px 10px;
}
.img-wrapper img {
  width: 100px;
}
.cart-wrapper{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  border: 1px solid #ccc;
  
    border-radius: 10px;
} 

.add-to-cart img{
  width: 30px;
  height: 36px;
}
.cart-count{
    z-index: 1;
    background-color: red;
    padding: 1px 8px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: #fff;
}
button.remove-btn{
  background-color: blueviolet;
  color: #fff;
}
